export function MonsterIcon({ iconType }: { iconType: string }) {
  //pre-prcessing
  iconType = iconType.toLowerCase();

  if (iconType.endsWith("icon")) {
    iconType = iconType.slice(0, -4);
  }

  //return icons
  if (iconType === "celestial") {
    return <CelestialIcon />;
  } else if (iconType === "aberration") {
    return <AberrationIcon />;
  } else if (iconType === "fiend") {
    return <FiendIcon />;
  } else if (iconType === "elemental") {
    return <ElementalIcon />;
  } else if (iconType === "dragon") {
    return <DragonIcon />;
  } else if (iconType === "ooze") {
    return <OozeIcon />;
  } else if (iconType === "monstrosity") {
    return <MonstrosityIcon />;
  } else if (iconType === "undead") {
    return <UndeadIcon />;
  } else if (iconType === "fey") {
    return <FeyIcon />;
  } else if (iconType === "beast") {
    return <BeastIcon />;
  } else if (iconType === "construct") {
    return <ConstructIcon />;
  } else if (iconType === "humanoid") {
    return <HumanoidIcon />;
  } else if (iconType === "giant") {
    return <GiantIcon />;
  } else if (iconType === "plant") {
    return <PlantIcon />;
  } else if (iconType === "ambusher") {
    return <AmbusherIcon />;
  } else if (iconType === "artillery") {
    return <ArtilleryIcon />;
  } else if (iconType === "defender") {
    return <DefenderIcon />;
  } else if (iconType === "bruiser") {
    return <BruiserIcon />;
  } else if (iconType === "controller") {
    return <ControllerIcon />;
  } else if (iconType === "leader") {
    return <LeaderIcon />;
  } else if (iconType === "skirmisher") {
    return <SkirmisherIcon />;
  } else {
    return "???";
  }
}

